* {
  margin: 0 ;
  padding: 0;
  box-sizing: border-box;
  font-family: Roboto, Open Sans, PT Sans;
}
:root {
  --primaryColor: #af9a7d;
  --mainWhite: #fff;
  --offWhite: #f7f7f7;
  --navColor: #273859;
  --secColor:#A0F2EA;
  --mainBlack: #222;
  --paraBlack:rgb(0, 0, 0);
  --mainGrey: #ececec;
  --darkGrey: #cfcfcf;
  --orange: #F28705;
  --green: #02732A;
  --darkOrange:#f24130;
--btnblue:rgb(36, 36, 114);

  --mainTransition: all 0.3s linear;
  --mainSpacing: 3px;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
}
/* globals */
body {
  /* padding-top: 66px; */
  /* color: var(--mainBlack);
  background: var(--mainWhite);
  
  line-height: 1.4; */
}
h1 {
  font-size: 3em;
