}
p{
  font-family: Roboto,Open Sans, PT Sans;
  font-size: 16px; 
  line-height: 150%;
  /* line-height: 180%; */
  color: var(--mainBlack);
  justify-content: space-around;
  font-weight: normal;
  text-align: left;

}
.space1{
 
  padding-left: 1em;

}
.space2{
 display: block;
  padding-left: 1.5em;

}
 
.font-awesome2{
    float:left;   
  color:#008FDB;
   margin-right:.5em; 
   margin-left:.5em; 
  
}

.btn-primary {
  display: inline-block;
  text-decoration: none;
  letter-spacing: var(--mainSpacing);
  color: var(--mainBlack);
  background: var(--primaryColor);
  padding: 0.4rem 0.9rem;
  border: 3px solid var(--primaryColor);
  transition: var(--mainTransition);
  text-transform: uppercase;
  cursor: pointer;
}
.btn-primary:hover {
  background: transparent;
  color: var(--primaryColor);
}
.loading {
  text-transform: capitalize;
  text-align: center;
  margin-top: 3rem;
}
.error {
  text-align: center;
  text-transform: uppercase;
  margin: 2rem 0;
}
.empty-search {
  text-align: center;
  text-transform: capitalize;
  margin: 2rem 0;
  padding: 1rem;
  letter-spacing: var(--mainSpacing);
}
/* end of globals */
/* Navbar */
.NavbarItems {
  height:95px;
  display:flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  font-weight: bold;
  padding-top: 1.8em;
   /* background: linear-gradient(90deg,rgb(110,94,254) 0%, rgba(73,63,252,1) 100%); */
   background-color:var(--mainWhite);
   
}
.navbar-logo{
  justify-self:start;
  /* margin-left: 20px; */
  /* padding: 20px; */
 

  text-decoration:none;
  cursor: pointer;

}
.logopart1{
  font-size: 28px;
  font-weight: 900;
  line-height: 30px;
  letter-spacing: 1.6px;
color: var(--orange);

}
.logopart2{
  font-size: 18px;
  color: var(--green);
  display: inline-block;
 
}

 .nav-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.nav-icon {
  font-size: 1.5rem;
  margin-left: .5em;
  color: var(--mainWhite);
}
.nav-menu{
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-gap: 10px;
 
  list-style: none;
   width: 70vw;
  justify-content: end;
  margin-right: 2rem;

}
.nav-links{
  position: relative;
  color: var(--paraBlack);
  /* color: var(--green); */
  font-size: 17px;
  font-weight: lighter;

  text-decoration: none;
  padding: .5rem 1rem 1.8em 1rem;

}

nav ul li ul{
  display: none;
  position: absolute;
  background-color:var(--offWhite);
  list-style: none;
  padding:10px;
 
}
.nav-menu li:hover ul {
  display: block;
  
}
.submenu-links{
  padding:1rem 1rem;
  
  font-weight: normal;
  text-decoration: none;
 
}

.nav-links:hover{
  /* padding-bottom:1.8em ;
  border-bottom: 3px solid var(--orange); */
  color: var(--darkOrange);

  transition: all .2s ease-out;
}
.submenu-links .nav-links{
  padding: .5rem 1rem .5rem 1rem;
}
.submenu-links .nav-links:hover{
  /* padding: .5rem 1rem .5rem 1rem; */
}
.nav-links-mobile{
  display: none;
}
.menu-icon{
  display: none;
  /* color: var(--mainWhite); */
}

/* end of Navbar */

