/* contact us */
.wrapper{
  /* position: absolute; */
 /* top: 50%; */
 /* transform: translate(-50%); */
 width:100%;
 /* padding-top: 2em; */
 display: grid;
 grid-template-columns: repeat(autofit, minmax(300,1fr));
 /* padding-bottom:2em 5em 4.3em 5em; */
 
}
.contactform{
  grid-column:1/-1 ;
/* max-width: 550px; */
background: var(--mainWhite);
margin:0 ;
padding: 2em 0 0 0;
display: flex;
flex-wrap: wrap;
align-items: center;
justify-content: center;
}
.contactform h3{
  padding-left: .5em;
}
.inputfields{
  display: flex;
  flex-wrap: wrap;

}
.inputfields p{
  flex-basis: 50%;
  padding: 0 2% ;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: space-between;
 
  }
.message{
  padding: 0 2% ;
  
}

  .inputfields p , .message p{
   padding-bottom: 2em;
  }

  .inputfields p label, .message p label, button{
    display: block;
    /* padding: .5em 0  ; */
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
   
  }
  .inputfields p input{
    height: 4em;
    border:1px solid var(--darkGrey);
    text-align: left;
    padding-left: 15px;
    font-size: 16px;
    color: var(--mainBlack);
    font-weight: bold;


  }
  
  .inputfields p input:focus, .message p textarea:focus,
  .inputfields p input:hover, .message p textarea:hover{
    outline:0;
    border-bottom: 2px solid var(--darkGrey);
  } 
 
  .message p textarea{
    height: 8em;
    border: 1px solid var(--darkGrey);
    padding: 20px 0 0 15px;
    font-size: 16px;
    color: var(--mainBlack);
    font-weight: bold;
  }
  .errorMessage {
    color: red;
    font-size: .9em;
    display: relative;
  }
  
  .submitbtn{

   
    /* width:50%; */
    
  }
  .message p button{
    font-size: 16px;
    color: var(--mainWhite);
    font-weight: bold;
     height: 3em;
    border: none;
    background: var(--btnblue);
    border-color: transparent;
    cursor: pointer;

  }
  .contactform button,  .contactform textarea, h3{
    width: 100%;
  
  }
  .contactform button:hover, .contactform button:focus{
    
    color: red;
    outline:0;
    
    transition: background-color 2s ease-out;
  }

/* end of contact us */



