@media screen and (max-width : 600px){
  
  .inputfields p{
    flex-basis: 100%;
  }
 
  .inputfields p {
  padding: .5em 1em;
  
  }
}

@media screen and (max-width : 768px){
  .about{
    /* flex-direction: column-reverse    ; */
    flex-flow: column wrap;
  }
}


@media screen and (max-width : 960px){
  .NavbarItems{
    position: relative;
  }
  .nav-menu{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 500px;
    position: absolute;
    top:80px;
    left:-100%;
    opacity: 1;
    transition: all .5s ease;
  }
  .nav-menu.active{
    background: var(--navColor);
    left: 0;
    opacity: 1;
    transition: all .5s ease;
    z-index: 1;

  }
  .nav-links{
    text-align: center;
    padding: 1rem;
    font-size: 1em;
    font-weight: 500; 
    margin: 0;
    width: 100%;
    display: table;
  }
  .nav-links:hover{
    background-color: #2983A6;
    border-radius: 0;

  }
  .navbar-logo{
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);

  }
  .menu-icon{
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;

  }
  
}


