.datacapture{
  display: grid;
  grid-template-columns: repeat(autofit, minmax(300,1fr));
  grid-template-rows:auto ;
  display:flex;
  flex-direction: column;
  margin-bottom: 4em;
}
.datacapture1{
    grid-column:1/-1 ;
   
    display:flex;
    flex-direction: column;
    flex-wrap: wrap;
    
   
}
.datacapture1 h3{
    /* flex-basis:100%; */
    padding: 2em 2em 0em 5.1em;
    font-size: 1.5em;
}
.datacapturepara1{
    /* flex-basis: 100%; */
    display: flex;
}

.datacapture11 {
  
    flex-basis: 50%;
    /* padding: 0em 0em 0em 8em; */
    /* padding: 0 2em; */
    
    }
    .datacapture11 p{
        
        padding: 0em 2em 2em 7em;
    }   

.datacapture2{
    grid-column:1/-1 ;
    flex-wrap: wrap;
    display:flex;
    /* padding: 0 4em; */
    padding: 0 7em 0 4em;
}
.datacapture21{
    /* padding: 0em 2em 4em 2em; */
    /* flex-basis: 50%; */
    padding: 0em 4em 2em 4em; 
    flex-basis: 100%;
}
.datacapture p{
   font-size: 18px;
    line-height: 180%;
   text-align: justify;
   
 }

