.insights{
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows:auto ;
    display:flex;
    flex-direction: column;
 
}
.insights1{
    grid-column:1/-1 ;
    display:flex;
    /* flex-wrap: wrap; */
    padding: 3em 4em;
}
.insights11{
    display: flex;
    flex-direction: column;

}
.insights11rtwrap{
display: flex;
flex-direction: column;
}
.insights11rtwrap h3{
    padding: 1em 2em 0em 2.6em;
    font-size: 1.5em;  
}
.insights11box{
   display: flex;
}
.insights11content{
    padding-right: 4em ;
    padding-left: 4em;
    flex-basis: 100%;
    }
    .insights11image{
     flex-basis:20%;
     }

     .insights12content{
        padding-right: 4em ;
        flex-basis: 80%;
        }
        .insights11image{
         flex-basis:20%;
         }
